A,CACA,wBA1BJ","sourcesContent":["\ntable {\n    thead {\n        position: sticky;\n        top: -1px;\n        left: 0;\n        width: 100%;\n        background: #fff;\n        z-index: 1;\n\n        tr {\n            th {\n\n            }\n        }\n    }\n\n    .order-list {\n        li {\n            &.enable-padding {\n                padding: 10px 0;\n\n                &:last-child {\n                    padding-bottom: 4px;\n                }\n            }\n\n            position: relative;\n\n            .line {\n                content: '';\n                position: absolute;\n                width: 100%;\n                height: 1px;\n                bottom: 0;\n                left: 0;\n                background-color: #d9d9d9;\n            }\n\n            &:last-child {\n                .line {\n                    content: '';\n                    position: absolute;\n                    width: 100%;\n                    height: 1px;\n                    bottom: 0;\n                    left: 0;\n                    background-color: transparent;\n                }\n            }\n        }\n    }\n\n    .checkbox-center {\n        width: 0;\n        text-align: center;\n    }\n\n    .ordering {\n        position: relative;\n\n        &:before {\n            right: 1em;\n            content: \"↑\";\n            position: absolute;\n            bottom: 0.9em;\n            display: block;\n            opacity: .3;\n        }\n\n        &:after {\n            right: 0.5em;\n            content: \"↓\";\n            position: absolute;\n            bottom: 0.9em;\n            display: block;\n            opacity: .3;\n        }\n    }\n\n    .ordering_asc {\n        position: relative;\n\n        &:before {\n            right: 1em;\n            content: \"↑\";\n            position: absolute;\n            bottom: 0.9em;\n            display: block;\n            opacity: 1;\n        }\n\n        &:after {\n            right: 0.5em;\n            content: \"↓\";\n            position: absolute;\n            bottom: 0.9em;\n            display: block;\n            opacity: .3;\n        }\n    }\n\n    .ordering_desc {\n        position: relative;\n\n        &:before {\n            right: 1em;\n            content: \"↑\";\n            position: absolute;\n            bottom: 0.9em;\n            display: block;\n            opacity: .3;\n        }\n\n        &:after {\n            right: 0.5em;\n            content: \"↓\";\n            position: absolute;\n            bottom: 0.9em;\n            display: block;\n            opacity: 1;\n        }\n    }\n}\n\n.loader-height {\n    padding-top: 10px;\n}\n\n.text-height {\n    height: 250px;\n    padding-top: 20px;\n}\n\n.table-filter {\n    display: flex;\n    align-items: center;\n    justify-content: center;\n    width: 170px;\n}\n\n.search-filter {\n    width: 250px;\n}\n\n.table-height {\n    height: 71vh;\n    overflow-x: hidden;\n    overflow-y: auto;\n    margin-bottom: 15px;\n}\n\n.table-control {\n    max-width: 180px;\n    min-width: 180px;\n    padding: 0 2rem 0 0.8rem;\n    font-size: 14px;\n}\n\n.btn-option {\n    display: flex;\n    align-items: center;\n    justify-content: center;\n    background-color: #f9f9f9;\n    border: 1px solid #cfcdcd;\n    width: 31px;\n    padding: 0;\n    height: 31px;\n    border-radius: 4px;\n    transition: 500ms;\n\n    &:hover {\n        background-color: darken(#f9f9f9, 10%);\n        transition: 500ms;\n    }\n}\n\n.table-avatar {\n    width: 45px;\n    height: 45px;\n    border-radius: 50%;\n    object-fit: fill;\n    object-position: center;\n    margin-right: 15px;\n}\n.table-image{\n    width: 100px;\n    height: 100px;\n}\n\n.no-list-style {\n    list-style: none;\n    padding-left: 0 !important;\n}\n"],"sourceRoot":""}]);
